import { Injectable } from '@angular/core';
import { Headers, Http } from '@angular/http';

import 'rxjs/add/operator/toPromise';

import { Part, Cars, CarMsg } from './dataType'

@Injectable()
export class MapdataService {

  private partUrl = 'assets/libs/serverJson/part.json';  // URL to web api
  private carUrl = 'assets/libs/serverJson/cars.json';  // URL to web api
  private carMsgUrl = 'assets/libs/serverJson/carMsg.json';  // URL to web api

  private headers = new Headers({ 'Content-Type': 'application/json' });

  constructor(private http: Http) { }

  getPart(): Promise<Part[]> {
    return this.http.get(this.partUrl)
      .toPromise()
      .then(response => response.json().result as Part[])
      .catch(this.handleError);
  }

  getCars(): Promise<Cars[]> {
    return this.http.get(this.carUrl)
      .toPromise()
      .then(response => response.json().result as Cars[])
      .catch(this.handleError);
  };

  getCarMsg(): Promise<CarMsg[]> {
    return this.http.get(this.carMsgUrl)
      .toPromise()
      .then(response => response.json().result as CarMsg[])
      .catch(this.handleError);
  };

  private handleError(error: any): Promise<any> {
    console.error('An error occurred', error); // for demo purposes only
    return Promise.reject(error.message || error);
  }

}



