import { Component, OnInit, ElementRef, Renderer, ViewChild, Output } from '@angular/core';
import { MapGridComponent } from '../map-grid/map-grid.component';
import { MapTreeComponent } from '../map-tree/map-tree.component';
import { MapdataService } from '../mapdata.service';

@Component({
  selector: 'app-main-interface',
  templateUrl: './main-interface.component.html',
  styleUrls: ['./main-interface.component.scss'],
  providers: [MapdataService]
})

export class MainInterfaceComponent implements OnInit {

  constructor(private el: ElementRef, renderer: Renderer, private heroSearchService: MapdataService) { }

  config = {
    ak: 'lnSwhHE1qYIsuyAAnHsLOlPAbSXDN0XD',
    initCity: '广州',
    zoom: 11,
    navigation: {
      anchor: 0,
      offset: {
        x: 10,
        y: 30
      }
    },
    scale: {
      anchor: 0,
    },
    cityList: {
      offset: {
        x: 80,
        y: 60
      },
      addBefore: function () {
        console.log('before');
      },
      addAfter: function () {
        console.log('after');
      }
    },
    insertUrls: {
      infoBoxShow: true,
      geoUtils: true,
      traffic: true,
      mkClusterer: true,
      searchRec: true
    }
  }

  $myMap = new TMap(this.config);

  // carsMarker = [];
  autoOnline = [];
  autoLeave = [];
  autoMyself = [];
  autoOut = [];


  tabShow = true;
  carsMarker = [];
  overlaysList = [];
  lastPoint; //上一次点的位置
  interval;
  isNomalMap: boolean = false;
  isTraffical: boolean = true;
  btnLock: boolean = true;
  mapTypeTitle: string = '切换至卫星地图';
  trafficTitle: string = '打开路况';
  adressText: string;
  saveBox: object;

  ngAfterViewInit() {

  }

  ngOnInit() {

    this.heroSearchService.getCars()
      .then((res): void => this.setInput(res));

    this.heroSearchService.getCarMsg()
      .then((res): void => this.carShow(res));
  }

  setInput(res): void {
    console.log(MapGridComponent);

  }

  showBox($event, index): void {
    console.log($event);
    var mainInterface = this;
    if (index) {
      mainInterface.$myMap.changeAttribute({
        center: true,
        zoom: 19,
        position: {
          lng: mainInterface.overlaysList[$event].point.lng,
          lat: mainInterface.overlaysList[$event].point.lat
        },
      })
    }

    mainInterface.$myMap.showInfoBox({
      marker: mainInterface.overlaysList[$event],
      sContent: ['<div class="showCarMassage"><table class="table"><tbody>' +
        '<tr><td class="pos">车牌号：</td><td>粤Y0Y793</td></tr>' +
        '<tr><td class="pos">定位时间：</td><td>2018-04-15 15:24:42</td></tr>' +
        '<tr><td class="pos">速度：</td><td>无(km/h)</td></tr>' +
        '<tr><td class="pos">坐标：</td><td><p>23.227341</p><p>113.08977</p><p></p></td></tr>' +
        '<tr><td class="pos">位置：</td><td>广东省佛山市南海区S361(永安大道西)联强电池附近32米</td></tr>' +
        '</tbody></table><ul class="operateCar">' +
        '<a id="carMonitor" class="col-sm-6"><i class="fa fa-crosshairs"></i>实时监控</a>' +
        '<a id="trackPlayback" class="col-sm-6"><i class="fa fa-caret-square-o-right"></i>轨迹回放</a>' +
        '</ul></div>'],
      boxStyle: null,
      boxOffset: {
        x: 0,
        y: 20
      },
      closeImgUrl: "assets/libs/images/close.png",
      closeFn: null,
      openFn: null,
      callback: function (e) {
        console.log(e);
      }
    })
  }

  carShow(carsMarker): void {
    let _mainInterface = this;
    let markerList = [];
    for (let i = 0; i < carsMarker.length; i++) {
      let carImg: string = "";
      if (carsMarker[i].online == 1) {
        carImg = "assets/libs/images/carMove.png";
      } else {
        carImg = "assets/libs/images/carStop.png";
      }
      if (carsMarker[i].trackInfo == null) {
        carsMarker[i].trackInfo = {
          lng: "",
          lat: "",
        }
      }
      let index = i;
      let typeM = {
        type: 1,
        attribute: {
          imgUrl: carImg,
          imgSize: {
            x: 32,
            y: 32
          },
          imgAnchor: {
            x: 16,
            y: 16
          },
          isAdd: false
        },
        name: carsMarker[i].vehicleId,
        pointList: [{
          lng: carsMarker[i].trackInfo.lng,
          lat: carsMarker[i].trackInfo.lat,
        }],
        othersObj: {
          data: carsMarker[i],
          index: index
        },
        callback: function (e) {
          _mainInterface.overlaysList[this.name] = e;
          _mainInterface.overlaysList[this.name].addEventListener("click", function (e) {
            let _this = this;
            _this.lastPoint = _this.point;
            _mainInterface.showBox(_this.data.vehicleId, false);
            // vehicleInfo = _this.data;
            // isInfoBoxClose = false;
          });
          markerList.push(_mainInterface.overlaysList[this.name]);
          if (_mainInterface.overlaysList[this.name].index == carsMarker.length - 1) {
            let mkType = {
              markers: markerList,
              callback: function (e) {
                console.log(e._clusters[0]._center);
                _mainInterface.$myMap.map.panBy(-170, 0);
              }
            }
            _mainInterface.$myMap.markerClusterer(mkType);
          }
        }
      };
      _mainInterface.$myMap.mapOverlay(typeM);
    }
  }

  //地图显示类型
  mapTypeTool($event): void {
    console.log($event.target);
    let element = $event.target;
    if (!this.isNomalMap) {
      this.$myMap.setMapType({
        type: 1
      });
      element.innerHTML = '&#xe601;';
      this.mapTypeTitle = '切换至普通视图';
    } else {
      this.$myMap.setMapType({
        type: 0
      });
      element.innerHTML = '&#xe603;';
      this.mapTypeTitle = '切换至卫星地图';
    }
    this.isNomalMap = !this.isNomalMap;
  };

  //是否打开路况
  showTraffic($event): void {
    let element = $event.target;
    if (this.isTraffical == true) {
      this.trafficTitle = '关闭路况';
    } else {
      this.trafficTitle = '打开路况';
    }
    this.$myMap.setTraffic('trafficBtn');
    this.isTraffical = !this.isTraffical;
  };

  pullSearch($event: any): void {
    console.log($event);
    let _this = this;
    let pullBoxType = {
      keyword: $event,
      name: _this.saveBox,
      callback: function (e) {
        // e.open();
        _this.saveBox = e;
      }
    }
    this.$myMap.pullBox(pullBoxType);
  }

  searchAddress($event: any): void {
    let _this = this;
    console.log(_this.adressText);
    _this.$myMap.searchAddr({
      text: _this.adressText
    })
  }
}



