import { Component, OnInit, ViewChild, Host, Inject, forwardRef } from '@angular/core';
import { jqxTreeComponent } from 'jqwidgets-scripts/jqwidgets-ts/angular_jqxtree';
import { MainInterfaceComponent } from '../main-interface/main-interface.component';
// import { jqxDropDownButtonComponent } from 'jqwidgets-scripts/jqwidgets-ts/angular_jqxdropdownbutton';
// import { jqxExpanderComponent } from 'jqwidgets-scripts/jqwidgets-ts/angular_jqxexpander';
import { MapdataService } from '../mapdata.service';

@Component({
  selector: 'app-map-tree',
  templateUrl: './map-tree.component.html',
  styleUrls: ['./map-tree.component.scss'],
  providers: [MapdataService]
})

export class MapTreeComponent implements OnInit {

  mainInterface: any;

  constructor(private heroSearchService: MapdataService, @Host() @Inject(forwardRef(() => MainInterfaceComponent)) mainInterface: MainInterfaceComponent) {
    this.mainInterface = mainInterface;
  }

  source = {
    datatype: 'json',
    datafields: [
      { name: 'id' },
      { name: 'parentid' },
      { name: 'text' },
      { name: 'icon' }
    ],
    id: 'id',
    localdata: [{
      "id": "141",
      "parentid": "-1",
      "text": "大沥右丰",
      "icon": "assets/libs/images/department.png"
    }]
  };

  dataAdapter = new jqx.dataAdapter(this.source, { autoBind: true });

  records: any = this.dataAdapter.getRecordsHierarchy('id', 'parentid', 'items', [{ name: 'text', map: 'label' }]);

  ngOnInit(): void {

    this.heroSearchService.getPart()
      .then((res): void => this.setTree(res))
      .then(() => setTimeout(() => {
        this.myTree.expandAll()
      }));

  }

  @ViewChild('myTree') myTree: jqxTreeComponent;

  setTree(res): void {
    this.source.localdata = res;
    this.dataAdapter = new jqx.dataAdapter(this.source, { autoBind: true });
    this.records = this.dataAdapter.getRecordsHierarchy('id', 'parentid', 'items', [{ name: 'text', map: 'label' }]);
  }

  myTreeOnInitialized(event: any): void {
    this.myTree.attrWidth = this.myTree.elementRef.nativeElement.parentNode.parentNode.parentNode.offsetWidth - 2;
    this.myTree.attrHeight = document.body.offsetHeight - 150;
  }



  ngAfterViewInit(): void {

  }


  ItemClick(event: any): void {
    // Do Something
    console.log(event.args.owner.selectedItem.id);
    // console.log(this.mainInterface);
    this.mainInterface.showBox(event.args.owner.selectedItem.id, 1);
  }

}


