import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';

import { MainInterfaceComponent } from './main-interface/main-interface.component';
import { TrackPlaybackComponent } from './track-playback/track-playback.component';
import { PlanTrackComponent } from './plan-track/plan-track.component';

const routes: Routes = [
    { path: '', redirectTo: '/mainInterface', pathMatch: 'full' },
    { path: 'mainInterface', component: MainInterfaceComponent },
    { path: 'planTrack', component: PlanTrackComponent },
    { path: 'trackPlayback', component: TrackPlaybackComponent }
];

@NgModule({
    imports: [RouterModule.forRoot(routes)],
    exports: [RouterModule]
})
export class AppRoutingModule { }


