import { Component, OnInit, ViewChild, AfterViewInit, Host, Inject, forwardRef } from '@angular/core';
import { MainInterfaceComponent } from '../main-interface/main-interface.component';
import { jqxGridComponent } from 'jqwidgets-scripts/jqwidgets-ts/angular_jqxgrid';
import { MapdataService } from '../mapdata.service';

@Component({
  selector: 'app-map-grid',
  templateUrl: './map-grid.component.html',
  styleUrls: ['./map-grid.component.scss'],
  providers: [MapdataService]
})

export class MapGridComponent implements OnInit {

  @ViewChild('gridReference') myGrid: jqxGridComponent;

  mainInterface: any;

  constructor(private heroSearchService: MapdataService, @Host() @Inject(forwardRef(() => MainInterfaceComponent)) mainInterface: MainInterfaceComponent) {
    this.mainInterface = mainInterface;
  }

  ngOnInit(): void {

    this.heroSearchService.getCarMsg()
      .then((res): void => this.setGrid(res));

  }

  setGrid(res): void {
    this.myGrid.setOptions({
      source: {
        localdata: res
      }
    });
  }

  ngAfterViewInit(): void {
    this.myGrid.setOptions({
      width: this.myGrid.elementRef.nativeElement.parentNode.parentNode.offsetWidth - 2,
      height: document.body.offsetHeight - 150,
      scrollbarsize: 1,
      autorowheight: true,
      columnsheight: 50
    });
  }

  source: any =
    {
      localdata: [{
        "vehicleId": 126,
        "plateNumber": "粤YZZ361",
        "loadWeight": 1495,
        "loadVolumn": 15,
        "autobrandName": "江铃汽车",
        "autouseTypeName": "其他",
        "automodelName": "宝典",
        "autoTags": 1,
        "autoTagsName": "自有车辆",
        "trackInfo": {
          "time": "2017-12-11 12:06:36",
          "speed": "-3.60",
          "opTags": 5,
          "radius": 65,
          "lng": 113.3210555150703,
          "lat": 23.14670379905952
        },
        "online": "0"
      }],
      datafields:
        [
          { name: 'plateNumber', type: 'string' },
          { name: 'autobrandName', type: 'string' },
          { name: 'loadWeight', type: 'number' },
          { name: 'loadVolumn', type: 'number' },
          { name: 'autoTagsName', type: 'string' }
        ],
      datatype: 'array'
    };

  dataAdapter: any = new jqx.dataAdapter(this.source);

  cellsrenderer = (row: number, columnfield: string, value: string | number, defaulthtml: string, columnproperties: any, rowdata: any): string => {
    // console.log(columnproperties);
    // console.log(rowdata);
    if (value < 20) {
      return '<span style="margin: 4px; float: ' + columnproperties.cellsalign + '; color: #ff0000;">' + value + '</span>';
    }
    else {
      return '<span style="margin: 4px; float: ' + columnproperties.cellsalign + '; color: #008000;">' + value + '</span>';
    }
  };


  columns: any[] =
    [
      { text: '<span>车牌号码</span>', datafield: 'plateNumber', align: 'center', width: ' 72px' },
      { text: '<span>车辆类型</span>', datafield: 'autobrandName', cellsalign: 'right', align: 'center', width: ' 72px' },
      { text: '<span class="specalColumn">装载重量<br />（kg）</span>', datafield: 'loadWeight', cellsalign: 'right', align: 'center', width: ' 72px' },
      { text: '<span class="specalColumn">装载体积<br />（m³）</span>', datafield: 'loadVolumn', cellsalign: 'right', align: 'center', width: ' 72px' },
      { text: '<span>车辆位置</span>', datafield: 'Discontinued', columntype: 'autoTagsName', align: 'center', cellsrenderer: this.cellsrenderer }
    ];

  // columngroups: any[] =
  //   [
  //     { text: 'Product Details', align: 'center', name: 'ProductDetails' }
  //   ];this.config,nomanate

  Cellclick(event: any): void {
    // Do Something
    console.log(event.args.row.bounddata.vehicleId);
    // console.log(this.mainInterface);
    this.mainInterface.showBox(event.args.row.bounddata.vehicleId, 1);
  }

}


